.directorist-row {
    display: flex;
    flex-wrap: wrap;
    margin-top: -15px;
    margin-right: -15px;
    margin-left: -15px;
}

.directorist-row>* {
    box-sizing: border-box;
    flex-shrink: 0;
    width: 100%;
    max-width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-top: 15px;
}

